<template>
    <div class="edit">
            <Alert v-if="alert" v-bind:message="alert" />
        <h1>Edit book</h1>
        <hr>
        <form v-on:submit="updateBook">
            <div class="wrapp">
                <h4>Book Info</h4>
                <div class="form-group">
                    <label>Image</label>
                    <input type="text" class="form-control" placeholder="Image url" v-model="book.img">
                </div>
                <div class="form-group">
                    <label>Genre</label>
                    <input type="text" class="form-control" placeholder="Genre" v-model="book.genre">
                </div>
                <div class="form-group">
                    <label>Title</label>
                    <input type="text" class="form-control" placeholder="Title" v-model="book.title">
                </div>
                <div class="form-group">
                    <label>Author</label>
                    <input type="text" class="form-control" placeholder="Author" v-model="book.author">
                </div>
                <div class="form-group">
                    <label>Publisher</label>
                    <input type="text" class="form-control" placeholder="Publisher" v-model="book.publisher">
                </div>
                <div class="form-group">
                    <label>Pages</label>
                    <input type="number" class="form-control" placeholder="Number of pages" v-model="book.pages">
                </div>
                <div class="form-group">
                    <label>Description</label> <br>
                    <textarea name="" id="" cols="30" rows="5" v-model="book.description"></textarea>
                </div>
                <button type="submit" class="btn btn-primary">Edit</button>
            </div>

        </form>
    </div>
</template>

<script>
    import Alert from './Alert'
    export default {
        data() {
            return {
                book: {},
                alert: ''
            };
        },
        methods: {
            updateBook(e) {
                if (
                    !this.book.title ||
                    !this.book.author ||
                    !this.book.img ||
                    !this.book.description ||
                    !this.book.publisher ||
                    !this.book.genre ||
                    !this.book.pages
                ) {
                    this.alert = 'Fill all required fields'
                } else {
                    let newBook = {
                        title: this.book.title,
                        genre: this.book.genre,
                        author: this.book.author,
                        publisher: this.book.publisher,
                        pages: this.book.pages,
                        img: this.book.img,
                        description: this.book.description
                    };

                    this.$http
                        .put("http://localhost:3000/api/books/"+this.$route.params.id, newBook)
                        .then(function (res) {
                            this.$router.push({ path: "/", query: { alert: "Book edited" } });
                        });
                    e.preventDefault();
                }
            },
            getBook (id){
                console.log(id)
                this.$http
                .get("http://localhost:3000/api/books/"+id)
                .then(function (res) {
                    
                    this.book = res.body[0]
                    console.log(this.book)
                });
            }
        },
        created() {
            this.getBook(this.$route.params.id);
        },
        components: {
            Alert
        }
    };
</script>

<style scoped>
    .wrapp {
        border: solid 1px #ccc;
        border-radius: 10px;
        padding: 20px;
        margin-bottom: 10px;
    }

    textarea {
        width: 100%;
    }

    button {
        width: 100px;
        height: 50px;
    }
</style>