<template>
    <div class="books">
        <Alert v-if="alert"  v-bind:message="alert"/>
        <h1>Manage your books</h1>
        <input class="form-control" type="text" placeholder="Enter a title" v-model="filterInput"> <br>
        
        <table class="table table-striped">
            <thead>
                <tr>
                    <th>Image</th>
                    <th>Title</th>
                    <th>Author</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="book in filterBy(books, filterInput)">
                    <td><img v-bind:src="book.img" class="imageBook" ></td>
                    <td>{{book.title}}</td>
                    <td>{{book.author}}</td>
                    <td><router-link class="btn btn-default" v-bind:to="'/details/'+book._id">View</router-link></td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import Alert from "./Alert";

export default {
  name: "books",
  data() {
    return {
      books: [],
      alert: "",
      filterInput: ""
    };
  },
  methods: {
    getBooks() {
      this.$http.get("http://localhost:3000/api/books").then(function(res) {
        this.books = res.body;
        // console.log(this.books)
      });
    },
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1)
      return list.filter(function(book){
        return book.title.indexOf(value) > -1
      })
    }
  },
  created() {
    this.getBooks();
    if (this.$route.query.alert) {
      this.alert = this.$route.query.alert;
    }
  },
  updated() {
    this.getBooks();
    
  },
  components: {
    Alert
  }
};
</script>

<style scoped>
    .imageBook{
        width: 50px;
    }
</style>