<template>
    <div>
        <h1 class="page-header">Book details</h1>
        <button class="btn btn-danger btn-small" v-on:click="deleteBook(book._id)">Delete</button>
        <router-link class="btn btn-primary" v-bind:to="'/edit/'+book._id">Edit</router-link>
      
        <ul class="list-group">
            <li class="list-group-item"><img v-bind:src="book.img" ></li>
            <li class="list-group-item"><span>Title: </span>{{book.title}}</li>
            <li class="list-group-item"><span>Author: </span>{{book.author}}</li>
            <li class="list-group-item"><span>Genres: </span>{{book.genre}}</li>
            <li class="list-group-item"><span>Pages: </span>{{book.pages}}</li>
            <li class="list-group-item"><span>Publisher: </span>{{book.publisher}}</li>
            <li class="list-group-item"><span>Description: </span>{{book.description}}</li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "details",
        data() {
            return {
                book: [],
            };
        },
        methods: {
            getBook(id) {
                this.$http
                    .get("http://localhost:3000/api/books/" + id)
                    .then(function (res) {
                        this.book = res.body[0];

                    });
            },
            deleteBook(id) {
                this.$http
                    .delete("http://localhost:3000/api/books/" + id)
                    .then(function (res) {
                        this.$router.push({ path: '/', query: {alert: 'Book removed'}})

                    });
            }
        },
        created() {
            this.getBook(this.$route.params.id);
        }
    };
</script>

