<template>
    <div class="add">
        <Alert v-if="alert" v-bind:message="alert" />
        <h1>Add book</h1>
        <hr>
        <form v-on:submit="addBook">
            <div class="wrapp">
                <h4>Book Info</h4>
                <div class="form-group">
                    <label>Image</label>
                    <input type="text" class="form-control" placeholder="Image url" v-model="book.img">
                </div>
                <div class="form-group">
                    <label>Genre</label>
                    <input type="text" class="form-control" placeholder="Genre" v-model="book.genre">
                </div>
                <div class="form-group">
                    <label>Title</label>
                    <input type="text" class="form-control" placeholder="Title" v-model="book.title">
                </div>
                <div class="form-group">
                    <label>Author</label>
                    <input type="text" class="form-control" placeholder="Author" v-model="book.author">
                </div>
                <div class="form-group">
                    <label>Publisher</label>
                    <input type="text" class="form-control" placeholder="Publisher" v-model="book.publisher">
                </div>
                <div class="form-group">
                    <label>Pages</label>
                    <input type="number" class="form-control" placeholder="Number of pages" v-model="book.pages">
                </div>
                <div class="form-group">
                    <label>Description</label> <br>
                    <textarea name="" id="" cols="30" rows="5" v-model="book.description"></textarea>
                </div>
                <button type="submit" class="btn btn-primary">Add</button>
            </div>

        </form>
    </div>
</template>

<script>
    import Alert from './Alert'
    export default {
        data() {
            return {
                book: {},
                alert: ''
            };
        },
        methods: {
            addBook(e) {

                if (!this.book.title || !this.book.author || !this.book.img || !this.book.description || !this.book.publisher || !this.book.genre || !this.book.pages) {
                    this.alert = 'Fill all required fields'
                } else {

                    let newBook = {
                        title: this.book.title,
                        genre: this.book.genre,
                        author: this.book.author,
                        publisher: this.book.publisher,
                        pages: this.book.pages,
                        img: this.book.img,
                        description: this.book.description
                    }

                    this.$http.post("http://localhost:3000/api/books", newBook).then(function (res) {
                        this.$router.push({ path: '/', query: { alert: 'Book added' } })
                    });
                    e.preventDefault();
                }

            }
        },
        components: {
            Alert
        }
    };
</script>

<style scoped>
    .wrapp {
        border: solid 1px #ccc;
        border-radius: 10px;
        padding: 20px;
        margin-bottom: 10px;
    }

    textarea {
        width: 100%;
    }

    button {
        width: 100px;
        height: 50px;
    }
</style>