import Vue from 'vue'
import Router from 'vue-router'
import Books from './components/Books.vue'
import About from './components/About.vue'
import AddBook from './components/AddBook.vue'
import Details from './components/BookDetails.vue'
import Edit from './components/EditBook.vue'


Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Books',
            component: Books
        },
        {
            path: '/about',
            name: 'About',
            component: About
        },
        {
            path: '/add',
            name: 'Add',
            component: AddBook
        },
        {
            path: '/details/:id',
            name: 'Details',
            component: Details
        },
        {
            path: '/edit/:id',
            name: 'Edit',
            component: Edit
        }
    ]
})